// students feedback

.shopping-area-start{
    position: relative;
    z-index: 1;
    padding: 100px 0 120px;
}
.ms-main {
    padding: 120px 0;

    .single-product {
        .ms-single-product {
            .banner-horizental {
                margin-bottom: 90px;

                .swiper {
                    overflow: hidden;

                    .swiper-wrapper {
                        .swiper-slide {
                            .slider-inner {
                                img {
                                    width: 100%;
                                }
                            }
                        }
                    }
                }

                .slider-pagination-area {
                    position: relative;

                    .swiper-pagination {
                        bottom: -120px;
                        text-align: center;
                        right: 50%;
                        transform: translateX(50%);
                        display: flex;
                        left: unset;
                        max-width: max-content;

                        .swiper-pagination-bullet {
                            width: 100px;
                            height: 100px;
                            border-radius: 0;
                            background: url(../images/shop/03.webp);
                            border-width: 2px;
                            border-style: solid;
                            border-color: #f9f9f9;
                            opacity: 1;
                            background-size: cover;
                            background-position: center;
                            margin-right: 5px;

                            @media(max-width:450px) {
                                width: 80px;
                                height: 80px;
                            }

                            &:first-child {
                                background: url(../images/shop/02.webp) !important;
                                background-size: cover !important;
                                background-position: center !important;
                            }

                            &:last-child {
                                background: url(../images/shop/04.webp) !important;
                                background-size: cover !important;
                                background-position: center !important;
                            }

                            &.swiper-pagination-bullet-active {
                                border-color: var(--color-primary);
                            }
                        }
                    }
                }
            }

            .ms-single-product__content {
                @media(max-width:991px) {
                    margin-top: 100px;
                    padding-left: 0;
                }

                .cart {
                    .single_add_to_cart_button {
                        line-height: 42px;
                    }
                }
            }

            .tab-area {
                .nav-tabs {
                    @media(max-width:520px) {
                        justify-content: center;
                    }

                    .nav-item {
                        margin-top: 0;
                        margin-bottom: 0;

                        @media(max-width:520px) {
                            margin-bottom: 10px;
                        }
                    }
                }

                .tab-content {
                    padding: 0;
                    line-height: 1.8;
                    margin-top: -1px;

                    .tab-pane {
                        .ms-heading-title {
                            font-size: 24px;
                            font-weight: 700;
                            margin: 1rem 0;
                        }

                        table {
                            tbody {
                                tr {
                                    &:last-child {
                                        th {
                                            border-bottom: none;
                                        }

                                        td {
                                            border-bottom: none;
                                        }
                                    }

                                    th {
                                        border-left: none;
                                        border-right: none;
                                        font-weight: 700;
                                        border: 1px solid #e2e2e2;
                                        border-left: 0;
                                        border-right: 0;
                                    }

                                    td {
                                        border-left: none;
                                        border-right: none;
                                        border: 1px solid #e2e2e2;
                                        border-left: 0;
                                        border-right: 0;
                                        padding-top: 14px;
                                        padding-bottom: 12px;
                                    }
                                }
                            }
                        }

                        .woocommerce-Reviews {
                            .full-details-inner {
                                .reveiw-form {
                                    .heading-title {
                                        font-size: 24px;
                                        font-weight: 700;
                                        margin: 1rem 0;
                                    }

                                    .contact-form {
                                        .input-box {
                                            width: 100%;

                                            textarea {
                                                width: 100%;
                                                padding: 0.5em 1em 0.5em 1em;
                                                background: none;
                                                border: 1px solid #DDD8F9;
                                                transition: all .4s;
                                                background: unset;
                                                color: #1F1F25;
                                                border-radius: 4px;

                                                @media(max-width:991px) {
                                                    margin-bottom: 25px;
                                                }

                                                &:hover {
                                                    border: 1px solid var(--color-primary);
                                                }

                                                &:focus {
                                                    outline: 0;
                                                    border-color: var(--color-primary);
                                                }

                                                &::placeholder {
                                                    color: #000000;
                                                }
                                            }

                                            input {
                                                width: 100%;
                                                padding: 0.5em 1em 0.5em 1em;
                                                background: none;
                                                height: 55px;
                                                margin-bottom: 25px;
                                                border: 1px solid #DDD8F9;
                                                margin-bottom: 25px;
                                                transition: all .4s;
                                                border-radius: 4px;
                                                color: #1F1F25;

                                                &:hover {
                                                    border: 1px solid var(--color-primary);
                                                }

                                                &:focus {
                                                    outline: 0;
                                                    border-color: var(--color-primary);
                                                    box-shadow: 0 0 0 4px var(--color-shadow);
                                                    background-color: var(--color-bg);
                                                }

                                                &::placeholder {
                                                    color: var(--color-body);
                                                }
                                            }
                                        }

                                        .rating {
                                            display: flex;
                                            align-items: center;
                                            margin-bottom: 25px;

                                            p {
                                                margin-bottom: 0;
                                            }
                                        }

                                        .btn {
                                            width: 100%;
                                            max-width: 100%;
                                            display: block;
                                            height: 50px;
                                            border-radius: 4px;
                                            color: #fff;
                                            font-size: 16px;
                                            padding: unset;
                                        }
                                    }
                                }
                            }
                        }
                    }
                    .rating-main-wrapper{
                        padding: 0;
                        border: none;
                    }
                }
            }
        }
    }
}

.ms-single-product .ms-single-product__content {
    padding-left: 50px;
    position: relative;
}

.ms-single-product .ms-single-product__content .ms-single-product_title {
    font-size: 48px;
    font-weight: 500;
    line-height: 1;
    margin-bottom: 2rem;
}

.ms-single-product .ms-single-product__content .price {
    margin-bottom: 3rem;
    display: inline-flex;
    flex-direction: row-reverse;
    align-items: baseline;
    font-size: 20px;
}

.ms-single-product .ms-single-product__content .price bdi {
    font-weight: 600;
    color: #110C2D;
}

.ms-single-product .ms-single-product__content .ms-woocommerce-product-rating {
    margin-bottom: 40px;
    .rating-area{
        display: flex;
        align-items: center;
        gap: 8px;
        span{
            color: #110C2D;
            font-size: 22px;
            font-style: normal;
            font-weight: 600;
            line-height: 26px; 
        }
        .stars{
            ul{
                padding-left: 0;
                display: flex;
                align-items: center;
                list-style: none;
                gap: 2px;
                margin: 0;
                li{
                    margin: 0;
                    i{
                        font-size: 16px;
                        color: #FFA41C;
                    }
                }
            }
        }
    } 
}

.ms-single-product .ms-single-product__content .ms-woocommerce-product-rating .ms-rating-icon svg {
    fill: #FFB03B;
}

.ms-single-product .ms-single-product__content .stock,
.ms-single-product .ms-single-product__content .woocommerce-product-details__short-description {
    margin-bottom: 2rem;
    line-height: 1.8;
}

.ms-single-product .ms-single-product__content .stock.in-stock {
    color: #2eb740;
}

.ms-single-product .ms-single-product__content .cart {
    display: flex;
}

.screen-reader-text {
    border: 0;
    clip: rect(1px, 1px, 1px, 1px);
    clip-path: inset(50%);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;
    word-wrap: normal !important;
}

.quantity-area {
    display: flex;
    margin-bottom: 30px;
    align-items: center;
    .cart-edit {
        .quantity-edit {
            background: none;
            border-radius: 0;
            padding: 0;
            input{
                border-radius: 0;
                border-width: 1px 0;
                border-color: #DDD8F9;
                color: #000000;
                font-weight: 500;
                &:focus{
                    border-color: #DDD8F9;
                }
            }
            button{
                padding: 14px 15px;
                border-color: #DDD8F9;
                color: #000000;
                &:focus{
                    border-color: #DDD8F9;
                }
                i{
                    color: #000;
                }
            }
        }
    }
    .rts-btn {
        padding: 10px 34px;
    }

    .single_add_to_cart_button {
        border-radius: 0;
        border: none;
        color: #FFFFFF;
        background-color: var(--color-primary);
        padding: 5px 12px;
        margin-left: 8pt;
        transition: background-color 0.3s cubic-bezier(0.645, 0.045, 0.355, 1), transform 0.1s cubic-bezier(0.645, 0.045, 0.355, 1);
    }
}

.product_meta {
    display: grid;

    span {
        margin-bottom: 15px;
    }
}

.ms-single-product__content,
.ms-woocommerce-cart-form-wrapper {

    .shop_table {
        th {
            border-top: none;
            font-size: 16px;
            font-weight: 500;
            color: #000000;
            padding-left: 0;
            text-align: center;
        }

        thead {
            @media(max-width:767px) {
                display: none;
            }
        }

        td {
            padding: 2rem 0;
            text-align: center;
            border-left: none;
            border-right: none;
            border: 1px solid #e2e2e2;

            @media(max-width:767px) {
                border: none !important;
            }
        }

        th,
        td {
            border-left: none;
            border-right: none;
        }

        th {
            padding: 15px 0;
        }

    }

    .shop_table tr {
        text-align: left;

        @media(max-width:767px) {
            display: -webkit-box;
            display: -webkit-flex;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-orient: horizontal;
            -webkit-box-direction: normal;
            -webkit-flex-direction: row;
            -ms-flex-direction: row;
            flex-direction: row;
            -webkit-box-pack: justify;
            -webkit-justify-content: space-between;
            -ms-flex-pack: justify;
            justify-content: space-between;
            -webkit-flex-wrap: wrap;
            -ms-flex-wrap: wrap;
            flex-wrap: wrap;
            -webkit-box-align: center;
            -webkit-align-items: center;
            -ms-flex-align: center;
            align-items: center;
            border-bottom: 1px solid #e8e8e8;
            margin-bottom: 40px;
        }
    }

    .product-remove {
        font-size: 28px;

        @media(max-width:767px) {
            width: 100% !important;
            font-size: 34px;
        }

        svg {
            @media(max-width:767px) {
                width: 32px;
            }
        }

    }

    .product-thumbnail {
        width: 10rem;

        @media(max-width:767px) {
            width: 100%;
        }

        img {
            width: 7rem;
            border-radius: 0;

            @media(max-width:767px) {
                width: 100%;
            }
        }

    }

    .product-name {
        font-size: 18px;
        font-weight: 600;

        @media(max-width:767px) {
            border: none !important;
            width: 100%;
            text-align: left !important;
            font-size: 22px;
        }

        a {
            color: #000000;
            transition: all .3s;
            font-weight: 500;

            &:hover {
                color: var(--color-primary);
            }
        }
    }

    .product-price {
        @media(max-width:767px) {
            width: 50%;
            text-align: left !important;
        }
    }

    .product-subtotal {
        @media(max-width:767px) {
            width: 100%;
            text-align: left !important;
        }
    }

    // Remove Btn
    .product-remove {
        width: 3rem;
        padding-left: 0;

        @media(max-width:767px) {
            border: none !important;
        }

        svg {
            fill: var(--color-contrast-higher);
            transition: fill .3s cubic-bezier(.645, .045, .355, 1);

            &:hover {
                fill: var(--color-error);
            }

        }
    }

    // Qty
    .ms-quantity {
        position: relative;
        width: 8rem;
        display: flex;

        .input-text {
            padding: 0.5em;
            height: 45px;
            border: none;
            width: 100%;
            text-align: center;
            color: #000000;
            background-color: #f7f7f7;
            appearance: none;

            &:focus-visible {
                outline: none;
            }
        }

        input::-webkit-outer-spin-button,
        input::-webkit-inner-spin-button {
            appearance: none;
            margin: 0;
        }

        .button-minus,
        .button-plus {
            border: none;
            background-color: transparent;
            display: flex;
            align-items: center;
            background-color: var(--color-contrast-lower);
            transition: background-color .3s cubic-bezier(.645, .045, .355, 1), transform .1s cubic-bezier(.645, .045, .355, 1);

            svg {
                width: 20px;
                stroke-width: 2pt;
                stroke: var(--color-contrast-higher);
            }

            &:hover {
                background-color: var(--color-contrast-low);
            }

            &:active {
                transform: translateY(2px);
            }
        }

        .button-minus {
            border-top-left-radius: 6pt;
            border-bottom-left-radius: 6pt;
            border-right: solid 1px var(--color-bg);

            svg {
                margin-left: 1pt;
            }
        }

        .button-plus {
            border-top-right-radius: 6pt;
            border-bottom-right-radius: 6pt;
            border-left: solid 1px var(--color-bg);

            svg {
                margin-right: 1pt;
            }
        }

    }

    // Cupon & Actions
    .ms-actions-inner {
        display: flex;
        justify-content: space-between;

        @media(max-width:767px) {
            display: block;
        }
    }

    .coupon {
        display: inline-flex;
        overflow: hidden;
        background-color: var(--color-contrast-lower);

        @media(max-width:767px) {
            width: 100%;
        }

        .button {
            padding: 10px 33px;
            color: var(--color-white);
            padding-left: 1.2rem;
            padding-right: 1.2rem;
            line-height: 1;
            border-radius: 0 4px 4px 0;
            min-width: max-content;

            @media(max-width:400px) {
                width: 50%;
            }
        }
    }

    .coupon+.button {
        padding-left: 1rem;
        padding-right: 1rem;
        background-color: var(--color-primary);
        color: var(--color-white);
        border: none;
        margin-right: 0;
        margin-left: auto;
        line-height: 2.4;
        font-size: 15px;
        border: solid 1px transparent;
        transition: transform .1s cubic-bezier(.645, .045, .355, 1);

        &:disabled {
            color: var(--color-contrast-high);
            background-color: transparent;
            border: solid 1px var(--color-contrast-high);

            @media(max-width:767px) {
                display: none;
            }
        }

        &:active {
            transform: translateY(2px);
        }
    }

    #coupon_code {
        border: none;
        background-color: #f7f7f7;
        padding: 0.5em 1em 0.5em 1em;
        height: 45px;

        @media(max-width:400px) {
            width: 50%;
        }

        &:focus-visible {
            outline: none;
        }
    }

    // Cart Totals
    .ms-cart-totals {
        margin-top: 6rem;
        margin-bottom: 6rem;
        background-color: #f7f7f7;
        padding: 2rem;

        .cart-subtotal,
        .order-total {
            td {
                text-align: right;
            }
        }

        tr {
            border-bottom: 1px solid #e2e2e2;
        }

        th,
        td {
            border: none;
            padding: 0 0 1rem;
            text-align: start;
        }

        .order-total {

            th,
            td {
                padding: 1rem 0 1rem;
            }
        }

        h3 {
            margin-bottom: 2rem;
            font-size: 24px;
            font-weight: 500;
        }

        .woocommerce-shipping-methods {
            padding-left: 0;
            list-style: none;
        }

        .woocommerce-shipping-destination {
            margin: 4pt 0;
        }

        .ms-proceed-to-checkout {
            margin-top: 3rem;
            display: flex;
            flex-direction: row-reverse;

            @media(max-width:767px) {
                flex-direction: unset;
            }

            a {
                padding: 0.5em 1.2em 0.5em 1.2em;
                height: 45px;
                color: #ffffff;
                display: flex;
                align-items: center;

                &:active {
                    transform: translateY(2px);
                }
            }
        }

        // Shiping
        .woocommerce-shipping-calculator {
            max-width: 100%;
            display: flex;
            flex-direction: column;

            .shipping-calculator-form {
                text-align: left;

                p {
                    max-width: 100%;
                    padding-top: 15px;

                    .input-text {
                        background-color: #f7f7f7;
                        width: 100%;
                        height: 45px;
                        padding-left: 1rem;
                        padding-right: 1rem;
                        border-radius: 0;
                        color: #000000;
                        transition: border-color 150ms var(--ease-in-out), box-shadow 150ms var(--ease-in-out), -webkit-box-shadow 150ms var(--ease-in-out);

                        &:hover {
                            border-color: var(--color-primary);
                        }

                        &:focus-visible {
                            z-index: 1;
                            border-color: var(--color-primary);
                            outline: 0;
                        }
                    }

                    .select2-container .select2-selection--single {
                        height: 45px;
                        display: flex;
                        align-items: center;
                        border-radius: 6pt;
                        background-color: var(--color-bg);
                        border: 1px solid #f7f7f7;
                    }

                    .select2-container--default .select2-selection--single .select2-selection__rendered {
                        color: var(--color-contrast-higher);
                    }

                    .select2-container .select2-selection--single .select2-selection__rendered {
                        padding-left: 1rem;
                        width: 100%;
                    }

                    .select2-container--default .select2-selection--single .select2-selection__arrow {
                        height: 45px;
                        right: 8pt;
                    }

                    .button {
                        height: 45px;
                        padding: 0 1rem;
                        border: none;
                        color: #ffffff;
                        border-radius: 6pt;
                        float: right;
                    }
                }

            }

        }

        .shop_table td {
            text-align: right;
        }
    }

}

.ms-woocommerce-MyAccount-content,
.ms-woocommerce-checkout {
    h3 {
        font-size: 24px;
        font-weight: 500;
        margin-bottom: 2rem;
        margin-top: 25px;
    }

    .woocommerce-EditAccountForm,
    .woocommerce-address-fields__field-wrapper,
    .woocommerce-billing-fields__field-wrapper {

        p.form-row {
            display: flex;
            flex-direction: column;
            margin-bottom: 1.5rem;

            label {
                font-size: 14px;
                margin-bottom: 4pt;

                .required {
                    text-decoration: none;
                }
            }

            .input-text {
                width: 100%;
                padding: 0.5em 1.2em 0.5em 1.2em;
                height: 45px;
                border: none;
                border-radius: 0;
                color: #000000;
                background-color: #f7f7f7;
                transition: border-color 150ms var(--ease-in-out), box-shadow 150ms var(--ease-in-out), -webkit-box-shadow 150ms var(--ease-in-out);

                &:hover {
                    border-color: var(--color-primary);
                }

                &:focus-visible {
                    z-index: 1;
                    border-color: var(--color-primary);
                    outline: 0;
                }
            }

            select {
                background: #f7f7f7;
                padding: 1em 1.2em 1em 1.2em;
            }
        }

        .select2-container--default .select2-selection--single {
            padding: 0.5em 1.2em 0.5em 1.2em;
            height: 45px;
            position: relative;
            border: solid 1px #e2e2e2;
        }

        .select2-container--default .select2-selection--single .select2-selection__arrow {
            height: 100%;
            top: 0;
            bottom: 0;
            right: 8px;
            width: 32px;
        }

        .select2-container--default .select2-selection--single .select2-selection__rendered {
            color: #000000;
            background-color: #f7f7f7;
            padding-left: 0;
        }

        .select2-container--default .select2-selection--single {
            background-color: #f7f7f7;
            transition: border-color 150ms var(--ease-in-out);

            &:hover {
                border-color: var(--color-primary);
            }
        }
    }

    .woocommerce-additional-fields {

        .form-row {
            display: flex;
            flex-direction: column;

            label {
                margin-bottom: 8pt;
            }

            .input-text {
                width: 100%;
                border-radius: 0;
                padding: 0.5em 1.2em 0.5em 1.2em;
                margin-bottom: 2rem;
                background-color: #f7f7f7;
                color: #000000;
            }
        }
    }

}

// Info
.woocommerce-form-coupon-toggle {
    margin-bottom: 3rem;
}

.wc_payment_methods {
    list-style: none;
    padding-left: 0;
}

.woocommerce-checkout-review-order {

    .wc_payment_methods,
    .woocommerce-terms-and-conditions-wrapper,
    .woocommerce-checkout-review-order-table {
        margin-bottom: 2rem;
    }

    .button {
        display: block;
        width: 100%;
        text-align: center;
        border: none;
        color: #fff;
        max-width: 100%;
        border-radius: 0;
    }

    .woocommerce-checkout-payment {
        @media(max-width:767px) {
            margin-bottom: 50px;
        }
    }
}

.woocommerce-info {
    padding: 2.5rem 2rem;
    border-radius: 6pt;
    background-color: #f7f7f7;

    .showcoupon {
        position: relative;
        color: var(--color-primary);
        text-decoration: none;
    }
}

// Review Order
.ms-checkout-review-order-table {

    .ms-checkout-product {
        display: flex;
        align-items: center;

        .ms-checkout-product__thumbnail img {
            width: 90px;
            margin-right: 20px;
        }

        .ms-checkout-product__content {
            h5 {
                font-size: 16px;
            }

            .woocommerce-Price-amount {
                color: var(--color-primary);
            }
        }
    }

    .cart_item td {
        border: none;
        padding: 0;
        padding-bottom: 2rem;
    }

    tfoot {

        th {
            text-align: left;
            padding-left: 0;
        }

        td {
            text-align: right;
            padding-right: 0;
        }

        th,
        td {
            border: none;
        }

        tr {
            border-bottom: dotted 1px #e2e2e2;
        }

        tr:last-child {
            border-bottom: none;
        }

        .woocommerce-shipping-methods {
            list-style: none;
        }
    }
}

// Checkout Coupon
.checkout_coupon.woocommerce-form-coupon {
    margin-bottom: 2rem;

    p:first-child {
        margin-bottom: 1rem;
    }

    .form-row-first,
